<template>
  <v-container id="statistics_t" fluid tag="section">
    <template v-if="!flag17">
      <v-skeleton-loader v-bind="attrs" type="article, actions" height="500px"></v-skeleton-loader>
    </template>

    <template v-else>
      <v-row>
        <v-tabs v-model="model">
          <v-tab v-for="(item, index) in times" :key="index" :href="`#tab-${index}`" @click="change(index)">{{ item }}</v-tab>
        </v-tabs>
      </v-row>

      <v-row>
        <v-col cols="12" md="3">
          <v-card height="510px" class="mx-auto">
            <v-toolbar color="primary">
              <v-text-field label="搜索" color="black" hide-details style="margin-top: 8px;">
                <template v-slot:append-outer>
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn class="mt-n2" elevation="1" fab small v-bind="attrs" v-on="on">
                        <v-icon>mdi-magnify</v-icon>
                      </v-btn>
                    </template>
                    <span>搜索</span>
                  </v-tooltip>
                </template>
              </v-text-field>
            </v-toolbar>
            <v-list two-line>
              <v-list-item-group v-model="pk" color="primary">
                <template v-for="(item, index) in classes">
                  <v-list-item :key="index" height="70px" @click="choose(index)">
                    <v-list-item-content>
                      <v-list-item-title v-html="item"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider v-if="index != classes.length" :key="index"></v-divider>
                </template>
              </v-list-item-group>
            </v-list>
          </v-card>
        </v-col>
        <v-col cols="12" md="9">
          <base-material-card color="warning" class="px-5 py-3" height="510px">
            <template v-slot:heading>
              <div class="display-2 font-weight-light">
                天梯榜
              </div>

              <div class="subtitle-1 font-weight-light">
                默认以月学习时长排名
              </div>
            </template>
            <v-card-text>
              <v-data-table :headers="headers" :items="items" />
            </v-card-text>
          </base-material-card>
        </v-col>
      </v-row>
    </template>

  </v-container>
</template>


<script>
  export default {
    name: 'statistics_t',

    data() {
      return {
        model: 'tab-2',
        flag17: false,
        current_class_id: "",
        headers: [{
            sortable: false,
            text: '学号',
            value: 'student_number',
          },
          {
            sortable: false,
            text: '姓名',
            value: 'name',
          },
          {
            sortable: true,
            text: '学习时长',
            value: 'train_time',
            align: 'right',
          },
          {
            sortable: true,
            text: '题量',
            value: 'practice_num',
            align: 'right',
          },
          {
            sortable: true,
            text: '正确率',
            value: 'accuracy',
            align: 'middle',
          },
        ],
        rank_list: [],
        flag15: false,
        items: [{
            student_number: 1,
            name: "李华",
            train_time: 30,
            practice_num: 1,
            accuracy: 22,
          },
          {
            student_number: 2,
            name: "张华",
            train_time: 30,
            practice_num: 30,
            accuracy: 75,
          },
          {
            student_number: 3,
            name: "小刘",
            train_time: 22,
            practice_num: 15,
            accuracy: 75,
          },
          {
            student_number: 4,
            name: "小润",
            train_time: 30,
            practice_num: 40,
            accuracy: 65,
          },
          {
            student_number: 5,
            name: "小泽",
            train_time: 23,
            practice_num: 30,
            accuracy: 50,
          },
        ],
        pk: 0,
        key: 0,
        times: ['本日', '最近一周', '最近一个月', '最近一年'],
        statistic_time: 'month',
        s_items: ['day', 'week', 'month', 'year'],

      }
    },
    created() {
      var that = this;
      that.get_classes_id()
      // that.get_classes()
      that.classes = that.$global.userMsg.classes
      setTimeout(() => {
        // console.log(that.rank_list[1].name);
        for (var i = 0; i < that.rank_list.length; i++)
        {
          if (that.statistic_time == 'month') {
            that.items[i].student_number = that.rank_list[i].student_number;
            that.items[i].name = that.rank_list[i].name;
            that.items[i].train_time = that.rank_list[i].train_time_lastmonth;
            that.items[i].practice_num = that.rank_list[i].train_questions_lastmonth;
            that.items[i].accuracy = that.rank_list[i].accuracy_lastmonth.toFixed(1).toString() + '%';
          }
          else if (that.statistic_time == 'week') {
            that.items[i].student_number = that.rank_list[i].student_number;
            that.items[i].name = that.rank_list[i].name;
            that.items[i].train_time = that.rank_list[i].train_time_lastweek;
            that.items[i].practice_num = that.rank_list[i].train_questions_lastweek;
            that.items[i].accuracy = that.rank_list[i].accuracy_lastweek.toFixed(1).toString() + '%';
          }
          else if (that.statistic_time == 'day') {
            that.items[i].student_number = that.rank_list[i].student_number;
            that.items[i].name = that.rank_list[i].name;
            that.items[i].train_time = that.rank_list[i].train_time_lastday;
            that.items[i].practice_num = that.rank_list[i].train_questions_lastday;
            that.items[i].accuracy = that.rank_list[i].accuracy_lastday.toFixed(1).toString() + '%';
          }
          else if (that.statistic_time == 'year') {
            that.items[i].student_number = that.rank_list[i].student_number;
            that.items[i].name = that.rank_list[i].name;
            that.items[i].train_time = that.rank_list[i].train_time_lastyear;
            that.items[i].practice_num = that.rank_list[i].train_questions_lastyear;
            that.items[i].accuracy = that.rank_list[i].accuracy_lastyear.toFixed(1).toString() + '%';
          }
        }
        //在这个for里进行判断和修改 if statistic_time = ..., ...
        setTimeout(() => {
          that.flag17 = true
          // that.$global.flag16 = true
          that.$forceUpdate();
        }, 6000)
      }, 500)
    },
    methods: {
      // get_classes: function () {
      //   var that = this;
      //   this.$axios({
      //     method: 'get',
      //     url: '/api/get_classes/',
      //     params: {
      //       tec_id: that.$global.userMsg.id,
      //     }
      //   })
      //   .then(function (response) {
      //     if (response.data.error_num == 0) {
      //       console.log(response.data)
      //       that.classes = response.data.classes
      //     } else if (response.data.error_num == 1) {
      //       alert("获取班级失败！")
      //     }
      //   })
      // },
      get_Info: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/get_static_info_by_class/',
          params: {
            id: that.current_class_id,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.rank_list = response.data.rank_list.reverse()
          } else {
            alert("查询失败！")
          }
        })
      },
      get_classes_id: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/get_classes_id/',
          params: {
            id: that.$global.userMsg.id,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.classes_id = response.data.classes_id
            that.current_class_id = that.classes_id[0]
            that.get_Info()
          } else {
            alert("查询失败！")
          }
        })
      },
      choose: function (index) {
        var that = this;
        this.current_class_id = this.classes_id[index]
        setTimeout(() => {
          that.get_Info()
        }, 300)
      },
      change: function (index) {
        var that = this;
        this.statistic_time = this.s_items[index];
        console.log("statistic_time = ", this.statistic_time)
        setTimeout(() => {
          for (var i = 0; i < that.rank_list.length; i++)
          {
            if (that.statistic_time == 'month') {
              that.items[i].student_number = that.rank_list[i].student_number;
              that.items[i].name = that.rank_list[i].name;
              that.items[i].train_time = that.rank_list[i].train_time_lastmonth;
              that.items[i].practice_num = that.rank_list[i].train_questions_lastmonth;
              that.items[i].accuracy = that.rank_list[i].accuracy_lastmonth.toFixed(1).toString() + '%';
            }
            else if (that.statistic_time == 'week') {
              that.items[i].student_number = that.rank_list[i].student_number;
              that.items[i].name = that.rank_list[i].name;
              that.items[i].train_time = that.rank_list[i].train_time_lastweek;
              that.items[i].practice_num = that.rank_list[i].train_questions_lastweek;
              that.items[i].accuracy = that.rank_list[i].accuracy_lastweek.toFixed(1).toString() + '%';
            }
            else if (that.statistic_time == 'day') {
              that.items[i].student_number = that.rank_list[i].student_number;
              that.items[i].name = that.rank_list[i].name;
              that.items[i].train_time = that.rank_list[i].train_time_lastday;
              that.items[i].practice_num = that.rank_list[i].train_questions_lastday;
              that.items[i].accuracy = that.rank_list[i].accuracy_lastday.toFixed(1).toString() + '%';
            }
            else if (that.statistic_time == 'year') {
              that.items[i].student_number = that.rank_list[i].student_number;
              that.items[i].name = that.rank_list[i].name;
              that.items[i].train_time = that.rank_list[i].train_time_lastyear;
              that.items[i].practice_num = that.rank_list[i].train_questions_lastyear;
              that.items[i].accuracy = that.rank_list[i].accuracy_lastyear.toFixed(1).toString() + '%';
            }
          }
          setTimeout(() => {
            that.$forceUpdate();
          }, 6000)
        }, 200)
      },
    }
  }
</script>